const mixin = {
  data() {
    return {
      downLoading: false, // 表头下拉加载
      goodsList: [] // 保存商品名称下拉列表
    }
  },
  computed: {
    // 处理仓库 下拉
    allWarehouseList() {
      let _warehouseList = this.warehouseList
      if (_warehouseList.length > 0) {
        _warehouseList.unshift({
          relation_warehouse_id: 0,
          warehouse_id: -1,
          warehouse_name: "全部仓库"
        })
      }
      return _warehouseList
    }
  },
  methods: {
    // 分页操作 - 切换请求条数
    moduleHandleSizeChange(val) {
      Object.assign(this.searchParams.params, {
        per_page: val,
        page: 1
      });
      this.getTableList(this.searchParams);
    },
    // 分页操作 - 切换请求页
    moduleHandleCurrentChange(val) {
      // 分页操作
      Object.assign(this.searchParams.params, {
        page: val
      });
      this.getTableList(this.searchParams);
    },
    // 表头 公司组织change 事件
    companyChange(val) {
      // 切换 组织时清空仓库
      if (this.params.warehouse_id != -1) {
        this.$set(this.params, "warehouse_id", "");
      }
    },
    // message 弹框提示
    messageTip(msg = "tip", type = "warning") {
      if (this._messageBox == undefined) {
        this._messageBox = [];
      } else {
        this._messageBox.forEach(item => {
          item.close();
        });
      }
      let _message = this.$message({
        message: msg,
        type: type
      });
      this._messageBox.push(_message);
    },
    // messageBox 询问弹框
    messageBox(params) {
      let {
        title = "提示", // 弹框提示title
        tipMsg = "", // 弹框提示语
        type = "warning", // 弹框默认提示类型
        conBtnText = "确定", // 确认按钮显示文字
        canBtnText = "取消", // 取消按钮显示文字
        cancelMsg, // 点击取消后弹框提示语
        callBack // 点击确定按钮后的回调
      } = params;
      this.$confirm(tipMsg, {
        title: title,
        confirmButtonText: conBtnText,
        cancelButtonText: canBtnText,
        showCancelButton: canBtnText ? true : false,
        dangerouslyUseHTMLString: true,
        type: type
      })
        .then(() => {
          callBack && callBack();
        })
        .catch(() => {
          if (cancelMsg) {
            this.$message({
              type: "info",
              message: cancelMsg
            });
          }
        });
    },
  },
  mounted() {
    let _table = this.$refs.tableName,
      _table_sum = this.$refs.table_sum;
    if (_table && _table_sum) {
      const elTable1 = _table.$el.getElementsByClassName(
        "el-table__body-wrapper"
      )[0];
      elTable1.addEventListener("scroll", function () {
        _table_sum.scrollLeft = this.scrollLeft;
      });
    }
  },
}
export default mixin;