<!-- 库存报警设置 -->
<template>
  <div class="stock_alarm" v-loading="submitLoading" element-loading-text="数据处理中，请等待" element-loading-spinner="el-icon-loading">
    <div class="search_container">
      <el-form :inline="true" :model="params" ref="formSearch">
        <el-form-item label="库存组织" size="mini" prop="company_id" :rules='[{required: true,trigger: "change", message: "请选择"}]'>
          <el-select clearable v-model="params.company_id" placeholder="请选择" filterable @focus="companyListInvok" @change="companyChange">
            <el-option v-for="item in companyList" :key="item.company_id" :label="item.company_name" :value="item.company_id">
              <span :class="item.type==1 ? 'optionStyle': '' ">{{ item.company_name }}</span>
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="商品编号" size="mini">
          <el-input type="text" v-model="params.goods_sn" placeholder="请输入"></el-input>
        </el-form-item>
        <el-form-item label="商品名称" size="mini">
          <el-select v-model="params.goods_id" placeholder="请选择" @focus="getGoodsList" filterable remote clearable reserve-keyword :loading="downLoading" :remote-method="getGoodsList">
            <ul class="warehouse-ul warehouse-ul2 warehouse-ul3">
              <li style="width:80px">商品编号</li>
              <li style="width: 200px;text-align:right">商品名称</li>
            </ul>
            <el-option v-for="(item,index) in goodsList" :label="item.goods_name" :value="item.goods_id" :key="index">
              <ul class="warehouse-ul  warehouse-ul3">
                <li style="width:80px ;color: #8492a6; font-size: 13px">{{ item.goods_id }}</li>
                <li style="width:200px" :title="item.goods_name">{{ item.goods_name }}</li>
              </ul>
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="仓库" size="mini">
          <el-select clearable v-model="params.warehouse_id" placeholder="请选择" @focus="warehouseInvok">
            <el-option :label="item.warehouse_name" :value="item.warehouse_id" :key="item.warehouse_id" v-for="item in allWarehouseList"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="品牌" size="mini">
          <getBrand v-model='params.brand_id' />
        </el-form-item>
        <el-form-item size="mini" class="search-style search">
          <el-button @click="onSearch" size="mini" type="primary">查询</el-button>
          <el-button plain @click="onReset" size="mini">重置</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="add_delet">
      <el-button icon="el-icon-circle-plus-outline" type="primary" @click="addF" :disabled="addType >= 3" class="addBtn" size="mini" v-if="authority.b_edit==1">新增</el-button>
      <el-button plain icon="el-icon-delete" @click="deletes" size="mini" v-if="authority.b_del==1">删除</el-button>
      <el-button plain icon="el-icon-edit-outline" @click="editlList" :disabled="addType == 2" size="mini" v-if="authority.b_edit==1">修改</el-button>
      <el-button plain icon="el-icon-tickets" @click="saveData('fromTable')" size="mini" :disabled="addType == 1">保存</el-button>
      <el-button @click="cancel" plain icon="el-icon-error" size="mini" v-if="addType >= 2">取消</el-button>
    </div>
    <div class="table_container">
      <el-form :rules="TableData.rules" class="tableForm" :model="TableData" size=mini ref="fromTable">
        <vxe-grid border ref="xTable" :show-header-overflow='true' show-overflow='tooltip' show-footer="true" :footer-method="footerMethod" :tooltip-config="{theme: 'light'}" :data="TableData.tableData" :columns="tableColumn" size="mini" resizable :max-height="380"></vxe-grid>
      </el-form>
      <div class="page_container">
        <div class="pageNumber">每页显示</div>
        <el-pagination class="erp-pagination" @size-change="moduleHandleSizeChange" @current-change="moduleHandleCurrentChange" :current-page.sync="goPage" :page-size="pageSize1" :page-sizes="per_page" layout="total,sizes, prev, pager, next,jumper" prevText="上一页" nextText="下一页" :total="TableData.total">
        </el-pagination>
      </div>
    </div>
    <!-- 导入弹框 -->
  </div>
</template>

<script>
import mixin from "../stockEarlyWarningMinxin.js";
import stockEarlyWarningIndex from "./stockEarlyWarningIndex.js";
import {
  getData, // 请求列表
} from "common/utils/public_function/commonUtils";
export default {
  name: "inventoryWarningStockEarlyWarningIndex",
  mixins: [mixin, stockEarlyWarningIndex],
  data() {
    return {
      submitLoading: false, // 提交添加加载
      addFlag: -1, // 新增的时候使用
      totalArr: [], // 设置合计配置
      headerTitle: [],
      loading: false,
      dataList: [],
      addType: 1, //新增or修改  1 新增(默认) 2 新增 3 修改
    };
  },
  methods: {
    // 接口 提交新增/修改
    portSaveData(params) {
      // this.submitLoading = true; <!-- 因接口处统一处理了 故不添加加载 -->
      let axMethod = "addserviceStockEarlyWarning"; // 默认新增
      if (this.addType == 3) axMethod = "editserviceStockEarlyWarning"; // 修改
      this.$api[axMethod](params)
        .then(({ data }) => {
          // this.submitLoading = false; // 解除加载
          if (data.code == 200) {
            this.messageTip(data.msg, "success");
            this.getTableList(this.searchParams); // 请求列表
          }
        })
        .catch((error) => {
          // this.submitLoading = false; // 解除加载
        });
    },
    // 接口 删除数据
    portDeletStock(params) {
      this.submitLoading = true;
      this.$api
        .deletserviceStockEarlyWarning(params)
        .then(({ data }) => {
          this.submitLoading = false; // 解除加载
          if (data.code == 200) {
            this.messageTip(data.msg, "success");
            this.getTableList(this.searchParams); // 请求列表
          }
        })
        .catch((error) => {
          this.submitLoading = false; // 解除加载
        });
    },
    // 接口 请求列表
    getTableList(params) {
      if (this.addType != 1) this.addType = 1;
      let _params = params;
      _params.TableData = this.TableData;
      _params.API = "getservice_stationWarningList";
      _params.vm = this;
      getData(_params, 2, ({ data }) => {
        this.$nextTick(() => {
          this.updateTableFooter();
        });
      });
    },
    // 导入
    dialogEmployee() {
      this.$refs.formSearch.validate((valid) => {
        if (valid) {
          this.options.params = {
            company_id: this.params.company_id,
          };
          // this.submitLoading = true; // 开启加载
          this.$refs.upload.stateOpen();
        }
      });
    },
    // 数据过滤
    dataFilter() {
      let list = [],
        filterField = {
          company_id: Number(localStorage.getItem("company_id")), // 库存组织(默认用户组织)
          company_name: "", // 组织名称
          goods_id: 0, // 商品编码
          goods_name: "", // 商品名称
          goods_sn: "", // 商品编号
          factory_code: "", // 工厂代码
          warehouse_id: 0, // 预警仓库
          warehouse_name: "", // 仓库名称
          brand_id: "", // 品牌
          brand_name: "", // 品牌名称
          unit_name: "", // 计量单位
          unit_id: 0, // 计量单位
          ceiling_inventory: 0, // 库存上限
          lower_inventory: 0, // 库存下限
          remark: "", // 备注
        };
      if (this.addType == 3) filterField.stock_early_warning_id = 0;
      this.TableData.tableData.forEach((item, index) => {
        if (item.edit) {
          if (!this.$refs[`vGoodsList${index}`].query) {
            // 用作渲染器中绑定值的判断
            this.$set(this.TableData.tableData[index], "goods_name", "");
            // this.TableData.tableData[index].goods_id = "";
          }
          let obj = this.$publicFution.filter(filterField, item);
          list.push(obj);
        }
      });
      return list;
    },
    // 点击保存
    saveData(ref) {
      let dataFilter = this.dataFilter(); // 要提交的数组
      this.$refs[ref].validate((valid) => {
        if (valid) {
          // 过滤 提交保存数据
          let _submitParams = {
            stock_early_warning_data: JSON.stringify(dataFilter),
          };
          this.portSaveData(_submitParams);
        }
      });
    },
    // 表体 仓库下拉 change 事件
    changeWarehouse(val, row) {
      let _warehouse_name = "";
      if (val || val == 0) {
        row.warehouseList.some((item) => {
          if (item.warehouse_id == val) {
            _warehouse_name = item.warehouse_name;
            return true;
          }
        });
      }
      row.warehouse_name = _warehouse_name;
    },
    // 表体 库存组织 change事件
    companyRowChange(val, row, index) {
      // 当切换组织后清除相关 字段信息
      let arr = [
        "goods_id",
        "goods_name",
        "goods_sn",
        "factory_code",
        "unit_name",
        "unit_id",
        "warehouse_id",
        "warehouse_name",
        "brand_id",
        "brand_name",
      ];
      arr.forEach((key) => {
        if (row[key]) {
          this.$set(row, key, "");
        }
      });
      let _company_name = "";
      if (val) {
        row.companyList.some((item) => {
          if (item.company_id == val) {
            _company_name = item.company_name;
            return true;
          }
        });
      }
      row.company_name = _company_name;
    },
    // 点击 新增或增行
    addF() {
      this.TableData.length = this.TableData.tableData.length;
      if (this.addType == 1) {
        this.clearCheckboxRow(); // 清空选中项
      }
      //取消按钮显示
      this.addType = 2; // 改变状态 增行
      this.addFlag += 1; // 作为删除的标记
      this.TableData.tableData.unshift({
        edit: true,
        add: `add${this.addFlag}`,
        company_id: Number(localStorage.getItem("company_id")),
        company_name: localStorage.getItem("company_name"),
        companyList: [
          {
            company_id: Number(localStorage.getItem("company_id")),
            company_name: localStorage.getItem("company_name"),
          },
        ],
      });
    },
    // 点击 修改
    editlList() {
      let selectArr = this.getSelectRecords();
      if (selectArr.length > 0) {
        this.addType = 3; // 改变状态 增行
        selectArr.forEach((item, index) => {
          if (!item.edit) {
            this.$set(item, "edit", true);
            item.warehouseList = [
              {
                warehouse_id: item.warehouse_id,
                warehouse_name: item.warehouse_name,
              },
            ];
            item.companyList = [
              {
                company_id: item.company_id,
                company_name: item.company_name,
              },
            ];
          }
        });
      } else {
        this.messageTip("请勾选需要修改的内容");
      }
    },
    // 点击 删除/删行
    deletes() {
      // this.addType 1 新增(默认) 2 增行 3 修改
      let selectionArr = this.getSelectRecords();
      if (selectionArr.length <= 0) {
        this.messageTip("请勾选需要删除的预警信息");
        return;
      }
      let _warning_idArr = [];
      selectionArr.forEach((item) => {
        if (item.add) {
          this.TableData.tableData.map((ele, i, arry) => {
            if (ele.add && item.add === ele.add) {
              arry.splice(i, 1);
              return ele;
            }
          });
        } else {
          _warning_idArr.push(item.stock_early_warning_id);
        }
      });
      this.addType = 1;
      if (_warning_idArr.length) {
        this.messageBox({
          tipMsg: `确定要删除${_warning_idArr.length}条已保存的单据？`,
          cancelMsg: "已取消删除",
          callBack: () => {
            this.portDeletStock({
              stock_early_warning_id: _warning_idArr.toString(),
            });
          },
        });
      }
      this.$nextTick(() => {
        this.clearCheckboxRow();
      });
    },
    // 点击 取消按钮
    cancel() {
      this.addType = 1; //重置状态 新增
      this.getTableList(this.searchParams);
    },
    // 获取默认 表头组织
    getDefaultCompany() {
      this.searchParams.params = {};
      // 默认带出操作员所属组织
      this.$set(this, "params", {
        company_id: Number(localStorage.company_id),
      });
      this.$set(this, "companyList", [
        {
          company_id: Number(localStorage.company_id),
          company_name: localStorage.company_name,
        },
      ]);
    },
    // 表头点击搜索
    onSearch() {
      this.$refs.formSearch.validate((valid) => {
        if (valid) {
          Object.assign(this.searchParams.params, this.params, {
            page: 1,
          });
          this.getTableList(this.searchParams);
        }
      });
    },
    // 表头点击重置
    onReset() {
      this.getDefaultCompany();
      Object.assign(this.searchParams.params, this.params, {
        page: 1,
      });
      this.getTableList(this.searchParams);
    },
    // 表体 库存组织下拉列表
    getCompanyList(parameter, row) {
      this.downLoading = true;
      this.$axMethod
        .getCompanyListDefalut({
          company_name: parameter,
        })
        .then(({ data }) => {
          this.downLoading = false;
          if (data.code == 200) {
            let result = data.data.company_list;
            if (result instanceof Array) {
              result.unshift({
                company_name: data.data.company_name,
                company_id: data.data.company_id,
                is_choose: data.data.is_choose,
                type: 1,
              });
              this.$set(
                row,
                "companyList",
                result.filter((item) => {
                  return item.is_choose == 1; // 是否可以被选择 1可以 0不可以
                })
              );
            }
          }
        })
        .catch((error) => {
          this.downLoading = false;
        });
    },
    // 表体 获取预警仓库列表
    getWareHouseList(row) {
      let params = {
        company_id: row.company_id,
      };
      this.downLoading = true;
      this.$axMethod
        .getWarehouseFocus(params)
        .then(({ data }) => {
          this.downLoading = false;
          if (data.code == 200) {
            let result = data.data;
            result.unshift({
              warehouse_id: 0,
              warehouse_name: "全部仓库",
            });
            this.$set(row, "warehouseList", result);
          }
        })
        .catch((error) => {
          this.downLoading = false;
        });
    },
    // 表体 商品名称选择
    handleSelect(value, scope) {
      let { row, rowIndex } = scope;
      if (value.disabled) {
        return false;
      }
      let _tableData = row;
      // 自动带出以下字段
      let arr = [
        "goods_id",
        "goods_name",
        "goods_sn",
        "factory_code",
        "unit_name",
        "unit_id",
        "brand_id",
        "brand_name",
      ];
      arr.forEach((key) => {
        this.$set(_tableData, key, value[key]);
      });
      // 切换之后刷新当前行
      this.tableReloadRow(_tableData);
    },
  },
  mounted() {
    this.getDefaultCompany(); // 执行获取默认组织 从本地获取localtion
    this.searchParams.params = {};
    Object.assign(this.searchParams.params, this.params, {
      page: 1,
    });
    this.getTableList(this.searchParams);
  },
};
</script>
